
body{
  font-family: 'Lato', sans-serif;
}

.sidebar{
  display: flex;
  flex-direction: column;
  width: 296px;
  padding-top: 40px;
  padding-bottom: 32px;
  background: #F8F9FA;
}
.logo{
  margin-bottom: 32px;
  margin-left: 32px;

}
.nav-item{
  margin-bottom: 24px;
}
.nav-item a{
  color:black;
}
.nav-item a:hover{
  color:green;
  border-left: 5px solid green;
}
.user{
  display: flex;
  margin-left: 32px;
  margin-top: auto;
}
.user-info p{
  margin-bottom: 0;
}
.user-photo img{
  width: 40px;
  height: 40px;
  border-radius: 24px;
  margin-right: 16px;
}
.user-info a{
  color:#6C757D;
  font-size: 14px;
  text-decoration: none;
}